@import 'variables.scss';

.App {
  // display: grid;
  // grid-template-rows: 1fr auto;
  min-height: 100vh; //magic
  background-color: #ffffff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 25px;
}

.page-container {
  min-height: 100vh;
}


// ypogtraphy
h1,
h2,
.header-title {
  font-family: 'Lalezar', sans-serif;
}


footer {
  margin-top: 1rem;
  padding: 0.5rem 0;
  background-color: $primaryLight;
  color: $green;
  text-align: center;
}

hr {
  border: none;
  border-bottom: solid 1px $lightGray;
}

// PROFILE

.user-info {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 1em;

  .user-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $darkText;
  }

  .user-avatar {
    margin-left: 5px;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    border: solid 1px $lightGray;
  }

  .user-avatar-pic {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 28px;
    color: $lightGray;
  }

  .user-name {
    font-size: 32px;
    margin-bottom: 3px;
  }
}

.user-info.compact {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 0.667rem;

  .user-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $darkText;
  }

  .user-caption {
    font-size: 11px;
  }

  .user-avatar {
    width: 50px;
    height: 50px;
  }

  .user-avatar-pic {
    font-size: 12px;
  }

  .user-name {
    font-size: 18px;
    margin-bottom: 3px;
  }
}



.grid-3-5 {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  border-bottom: solid 1px #ddd;
  margin-bottom: 2rem;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.grid-5 {
  display: grid;
  gap: 1rem;
}

@media screen and (min-width: 900px) {
  .grid-3-5 {
    grid-template-columns: 3fr 5fr;
  }

  .grid-2 {
    grid-template-columns: 1fr 1fr;
  }

  .grid-5 {
    grid-template-columns: repeat(5, 1fr);
  }
}

// Analysis
.analysis {
  text-align: center;

  h2,
  h3 {
    margin-bottom: 0;
  }

  h3 {
    margin-bottom: 0;
    margin: 0.667rem -25px 0;
    padding-bottom: 0.15rem;
    border-bottom: solid 1px $lightGray;
  }

  p {
    margin-top: 0.25rem;
    margin-bottom: 0.5rem;
  }

  .column-header {
    margin: 0 -25px;
    padding: 3px 0;

    border-bottom: solid 1px $lightGray;
  }

  // ANALYSIS

  .analysis-player-list {
    margin: 0 -10px;
  }

  .analysis-player-container {
    display: grid;
    grid-template-columns: 1fr auto;

    text-align: initial;
    margin-top: 1rem;

  }

  .analysis-player-value {
    display: flex;
    align-items: center;
    margin-left: 8px;

    font-family: monospace;
    font-size: 17px;

    span+span {
      margin-left: 8px;
    }

    span:nth-child(1) {
      color: $lighterText;
    }

    span:nth-child(2) {
      color: #bbb;
    }
  }

  @media screen and (min-width: 900px) {
    .column-header {
      margin-top: 0;
      margin-bottom: 0;

    }

    .analysis-player-list {
      padding: 0 28px 16px 16px;

      height: 340px;
      overflow: scroll;
      box-shadow: $box-shadow-inner;
      border: solid 1px #eee;
      border-top: none;

    }
  }

}

/* Data grid */
.grid-left {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 8px 12px;
}

@media screen and (min-width: 700px) {
  .grid-left {
    grid-template-columns: auto 1fr;
  }
}


.grid-label {
  text-transform: uppercase;
  font-size: smaller;
  color: $lightText;
  background: $lighterGray;
  padding: 3px 5px 3px 9px;
  border-radius: 1px;

  text-align: right;
}

.grid-value {
  display: flex;
  align-items: center;
  // justify-content: center;
}