@import url(https://fonts.googleapis.com/css2?family=Lalezar&family=Montserrat&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body,.root{min-height:100vh}body{margin:0;font-family:'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fff;color:#1e1e1e}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{min-height:100vh;background-color:#ffffff}.container{max-width:1200px;margin:0 auto;padding:0 25px}.page-container{min-height:100vh}h1,h2,.header-title{font-family:'Lalezar', sans-serif}footer{margin-top:1rem;padding:0.5rem 0;background-color:#1e1e1e;color:#61b510;text-align:center}hr{border:none;border-bottom:solid 1px #dbdbdb}.user-info{display:grid;grid-template-columns:auto 1fr;grid-gap:1em}.user-info .user-text{display:flex;flex-direction:column;justify-content:center;color:#383838}.user-info .user-avatar{margin-left:5px;width:90px;height:90px;border-radius:50%;border:solid 1px #dbdbdb}.user-info .user-avatar-pic{display:flex;justify-content:center;align-items:center;height:100%;font-size:28px;color:#dbdbdb}.user-info .user-name{font-size:32px;margin-bottom:3px}.user-info.compact{display:grid;grid-template-columns:auto 1fr;grid-gap:0.667rem}.user-info.compact .user-text{display:flex;flex-direction:column;justify-content:center;color:#383838}.user-info.compact .user-caption{font-size:11px}.user-info.compact .user-avatar{width:50px;height:50px}.user-info.compact .user-avatar-pic{font-size:12px}.user-info.compact .user-name{font-size:18px;margin-bottom:3px}.grid-3-5{display:grid;grid-template-columns:1fr;grid-gap:1rem;gap:1rem;border-bottom:solid 1px #ddd;margin-bottom:2rem}.grid-2{display:grid;grid-template-columns:1fr;grid-gap:2rem;gap:2rem;margin-bottom:2rem}.grid-5{display:grid;grid-gap:1rem;gap:1rem}@media screen and (min-width: 900px){.grid-3-5{grid-template-columns:3fr 5fr}.grid-2{grid-template-columns:1fr 1fr}.grid-5{grid-template-columns:repeat(5, 1fr)}}.analysis{text-align:center}.analysis h2,.analysis h3{margin-bottom:0}.analysis h3{margin-bottom:0;margin:0.667rem -25px 0;padding-bottom:0.15rem;border-bottom:solid 1px #dbdbdb}.analysis p{margin-top:0.25rem;margin-bottom:0.5rem}.analysis .column-header{margin:0 -25px;padding:3px 0;border-bottom:solid 1px #dbdbdb}.analysis .analysis-player-list{margin:0 -10px}.analysis .analysis-player-container{display:grid;grid-template-columns:1fr auto;text-align:left;text-align:initial;margin-top:1rem}.analysis .analysis-player-value{display:flex;align-items:center;margin-left:8px;font-family:monospace;font-size:17px}.analysis .analysis-player-value span+span{margin-left:8px}.analysis .analysis-player-value span:nth-child(1){color:#777}.analysis .analysis-player-value span:nth-child(2){color:#bbb}@media screen and (min-width: 900px){.analysis .column-header{margin-top:0;margin-bottom:0}.analysis .analysis-player-list{padding:0 28px 16px 16px;height:340px;overflow:scroll;box-shadow:inset 0 2px 4px 0 rgba(0,0,0,0.06);border:solid 1px #eee;border-top:none}}.grid-left{display:grid;grid-template-columns:auto 1fr;grid-gap:8px 12px}@media screen and (min-width: 700px){.grid-left{grid-template-columns:auto 1fr}}.grid-label{text-transform:uppercase;font-size:smaller;color:#515151;background:#f7f7f7;padding:3px 5px 3px 9px;border-radius:1px;text-align:right}.grid-value{display:flex;align-items:center}

input,select,button{padding:3px;-webkit-appearance:none;outline:none}input{border:1px solid #aaa;padding:.3em 1.4em .3em .8em;font-size:16px}a.panel{display:inline-block}.panel{padding:1rem;border-radius:7px;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);color:#383838}form>hr{margin:1rem -1rem}fieldset{margin-bottom:1.5rem}a.btn{text-decoration:none;display:inline-flex}.btn{padding:12px 25px;font-weight:600;background:none;color:#dbdbdb;border-radius:7px;border:none;cursor:pointer}.btn+.btn{margin-left:1em;margin-bottom:0.5em}.btn-sm{padding:10px 20px}.btn.btn-sm:not(.btn-theme){border:solid #1e1e1e 1px;color:#1e1e1e;font-weight:400}.btn.btn-theme{color:#61b510;font-weight:400;background:#1e1e1e;border-bottom:none;box-shadow:0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);transition:all 200ms}.btn.btn-theme:hover{background:#2b2b2b}.btn.btn-unstyled{background-color:inherit;border:none;padding:2px 6px;color:#777}.button-unstyled>*{pointer-events:none}.form-wrapper{max-width:48em;margin:0 auto}.form-control::-moz-placeholder,.form-control:-ms-input-placeholder,.form-control::-webkit-input-placeholder{color:#dbdbdb}.form-control{height:50px;font-size:13px;color:#383838;background:none;border-radius:4px;border-bottom:2px solid #1e1e1e}.theme-bg .form-control,.dark-bg .form-control{background:none;border-bottom-color:#fff;color:#fff}.grey-bg .form-control{background:#fff}textarea.form-control{height:150px}.form-control:focus{box-shadow:none;background:none;border-color:#1e1e1e}select.form-control{-moz-appearance:none;-webkit-appearance:none;appearance:none;height:50px !important}.form-radio{-webkit-appearance:none;-moz-appearance:none;appearance:none;display:inline-block;position:relative;top:10px;height:30px;width:30px;margin-right:10px;color:#515151;background-color:#fff;border:solid 1px #1e1e1e;border-radius:50px;outline:none;cursor:pointer;transition:all 200ms}.form-radio:checked::before{position:absolute;font:13px/1 'Open S