select-css:disabled,.select-css[aria-disabled=true]{color:graytext;background-image:url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22graytext%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),linear-gradient(to bottom, #fff 0%, #f7f7f7 100%)}.select-css:disabled:hover,.select-css[aria-disabled=true]{border-color:#aaa}

.lds-dual-ring{display:inline-block;width:80px;height:80px}.lds-dual-ring:after{content:' ';display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #1e1e1e;background:#833ab4;background:linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.spinner{width:70px;text-align:center}.spinner>div{width:1rem;height:1rem;border-radius:100%;display:inline-block;-webkit-animation:sk-bouncedelay 1.4s infinite ease-in-out both;animation:sk-bouncedelay 1.4s infinite ease-in-out both;background-color:#1e1e1e}.spinner .bounce1{-webkit-animation-delay:-0.32s;animation-delay:-0.32s}.spinner .bounce2{-webkit-animation-delay:-0.16s;animation-delay:-0.16s;background-color:#61b510}@-webkit-keyframes sk-bouncedelay{0%,80%,100%{-webkit-transform:scale(0)}40%{-webkit-transform:scale(1)}}@keyframes sk-bouncedelay{0%,80%,100%{transform:scale(0)}40%{transform:scale(1)}}

header{position:-webkit-sticky;position:sticky;top:0;z-index:100;width:100%;padding:0.6rem 1.2rem;background-color:#1e1e1e}.header-title a{text-decoration:none;color:#fff;font-weight:bold;font-size:28px}.header-tagline{font-size:smaller;color:#777}.btn.button-unstyled.menu-button{float:right;color:#61b510;text-transform:uppercase;font-family:'Lalezar'}.nav-list{margin-top:0;padding:0;margin-bottom:1rem}.nav-list>li{display:inline}.nav-list>li+li{margin-left:1rem}ul.position-list{padding:0}ul.position-list li{display:inline-flex}ul.position-list li+li{margin-left:1rem}a.nav-link{display:inline-block;padding:3px 4px;color:#61b510;text-decoration:none;border-bottom:solid 1px transparent}a.nav-link:hover{border-bottom:solid 1px #61b510;transition:all 250ms}a.nav-link.active,a.nav-link.active:hover{border-bottom:solid 1px #61b510}.desktop-menu,.header-tagline{display:none}@media screen and (min-width: 700px){.header-grid{display:grid;grid-template-columns:auto 1fr;grid-gap:0 2em;text-align:left;text-align:initial}.header-grid>div{z-index:1;display:flex;flex-direction:column;justify-content:center}.header-title{font-size:24px;color:#fff}nav.desktop-grid{display:grid;grid-template-columns:1fr auto}.nav-list{margin-top:0;margin-bottom:0;padding:0}.nav-list>li{margin-bottom:0;display:inline-block}.nav-list>li+li{margin-left:1em}.header-tagline{display:inline;display:initial}.mobile-menu,.btn.button-unstyled.menu-button{display:none}}

