@import 'variables.scss';


input,
select,
button {
  padding: 3px;
  -webkit-appearance: none;
  outline: none;
}

input {
  border: 1px solid #aaa;
  padding: .3em 1.4em .3em .8em;
  font-size: 16px;
}

a.panel {
  display: inline-block;
}

.panel {
  padding: 1rem;
  // border: solid 1px $lightGray;
  border-radius: 7px;
  box-shadow: $box-shadow-medium;
  color: $darkText;

}

form>hr {
  margin: 1rem -1rem;
}


fieldset {
  margin-bottom: 1.5rem;
}


// Buttons
// Buttons
// Buttons

a.btn {
  text-decoration: none;
  display: inline-flex;
}

.btn {
  padding: 12px 25px;
  font-weight: 600;
  background: none;
  color: $lightGray;
  border-radius: 7px;
  border: none;
  cursor: pointer;
}

.btn+.btn {
  margin-left: 1em;
  margin-bottom: 0.5em;
}

.btn-sm {
  padding: 10px 20px;
}

.btn.btn-sm:not(.btn-theme) {
  border: solid $primaryLight 1px;
  color: $primaryLight;
  font-weight: 400;
}

.btn.btn-theme {
  color: $green;
  // font-family: 'Open Sans', sans-serif;
  font-weight: 400;

  background: $primaryLight;
  border-bottom: none;
  box-shadow: $box-shadow-medium;

  transition: all 200ms;
}

.btn.btn-theme:hover {
  background: lighten($color: $primaryLight, $amount: 5);
}

.btn.btn-unstyled {
  background-color: inherit;
  border: none;
  padding: 2px 6px;
  color: $lighterText;
}

.button-unstyled>* {
  pointer-events: none;
}

