// FormControl
// FormControl
// FormControl

.form-control {
  height: 50px;
  font-size: 13px;
  color: $darkText;
  background: none;
  // border: none;
  border-radius: 4px;
  border-bottom: 2px solid $primaryLight;
}

.theme-bg .form-control,
.dark-bg .form-control {
  background: none;
  border-bottom-color: $white;
  color: $white;
}

.grey-bg .form-control {
  background: $white;
}

textarea.form-control {
  height: 150px;
}

.form-control:focus {
  box-shadow: none;
  background: none;
  border-color: $primaryLight;
}

// .theme-bg .form-control:focus {
//   border-color: $primaryColor;
// }

select.form-control {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  height: 50px !important;
}



// FormControl
// FormControl
// FormControl

// Radio
// Radio
// Radio

.form-radio {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  position: relative;
  top: 10px;
  height: 30px;
  width: 30px;
  margin-right: 10px;
  color: $lightText;
  background-color: $white;
  border: solid 1px $primaryLight;
  border-radius: 50px;

  outline: none;
  cursor: pointer;
  transition: all 200ms;
}

.form-radio:checked::before {
  position: absolute;
  font: 13px/1 'Open Sans', sans-serif;
  left: 10px;
  top: 6px;
  content: '\02143';
  transform: rotate(40deg);
}

.form-radio:hover {
  border: solid 1px $primaryLight;
  color: $lightGray;
  content: '\02143';
}

.form-radio:checked {
  color: $white;
  background-color: $primaryLight;
  border: solid 1px $primaryLight;
}

.form-radio:disabled,
.form-control:disabled,
.form-control[readonly] {
  color: $darkText;
  border: 1px solid $lightGray;
  background-color: $lighterGray;
  box-shadow: $box-shadow-inner;
}

